.main-menu{
   background-color: black;
   top: 0;
   left: 0;
   position: fixed;
   width: 100%;
   z-index: 1000;
   height: 50px;
   overflow: hidden;
   color: white;
   -webkit-transition: all .5s ease;
   -o-transition: all .5s ease;
   transition: all .5s ease;
}

.main-menu-form {
   z-index: 1001;
   height: 40px;
   width: 100%;
}

.main-menu.open{
   height: 290px;
}

.main-menu-close{
   display: none;
}

@media (min-width: 768px) {
   /*main menu*/
   .main-menu {
      top: 0;
      left: 0;
      position: fixed;
      height: 175px;
      width: 475px;
      z-index: 1000;
      -webkit-transition: all 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) ease-in-out;
      -o-transition: all 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) ease-in-out;
      transition: all 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) ease-in-out;
   
      -webkit-transform-origin: 100% 0;
      -ms-transform-origin: 100% 0;
          transform-origin: 100% 0;
   
      -webkit-transform: translate(-336px, 0);
      -ms-transform: translate(-336px, 0);
          transform: translate(-336px, 0);
      background-color: transparent;
   }
   .main-menu.open {
      -webkit-transform: translate(0, 0) skew(0);
      -ms-transform: translate(0, 0) skew(0);
          transform: translate(0, 0) skew(0);
      min-height: 100%;
   }
   .main-menu.open .main-menu-form {
      -webkit-transform: skewX(-25deg);
      -ms-transform: skewX(-25deg);
          transform: skewX(-25deg);
   }
   .main-menu.open .main-menu-form:after {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      width: 1px;
      height: 100%;
      background: #2980B9;
      -webkit-box-shadow: 0px 0px 6px 1px rgba(93, 173, 226, 0.7);
              box-shadow: 0px 0px 6px 1px rgba(93, 173, 226, 0.7);
   }
   .main-menu-form {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: #283747;
      z-index: 1001;
      -webkit-transition: all 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19);
      -o-transition: all 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19);
      transition: all 0.2s cubic-bezier(0.55, 0.055, 0.675, 0.19);
   
      -webkit-transform-origin: 100% 0;
      -ms-transform-origin: 100% 0;
          transform-origin: 100% 0;
   
      -webkit-transform: skewX(-38deg);
      -ms-transform: skewX(-38deg);
          transform: skewX(-38deg);
   }
   
   .main-menu.open .main-menu-btn {
      opacity: 0;
      visibility: hidden;
      -webkit-transition: opacity 0s, visibility 0s;
      -o-transition: opacity 0s, visibility 0s;
      transition: opacity 0s, visibility 0s;
   }
   
   /*main menu content*/
   .main-menu-content {
      position: relative;
      padding: 35px 55px 35px 25px;
      font: normal 16px/1.25 'Open Sans';
      display: none;
   }

   .main-menu-content.open {
      display: inline-block !important;
   }

   .main-menu.open .main-menu-content {
      z-index: 1002;
   }
   .main-menu-logo h2 {
      font-size: 28px;
      opacity: 0;
   }
   .main-menu.open .main-menu-logo h2 {
      opacity: 1;
      -webkit-transition: opacity .5s ease-in .3s;
      -o-transition: opacity .5s ease-in .3s;
      transition: opacity .5s ease-in .3s;
   }
   .main-menu-nav {
      margin-top: 35px;
   }
   
   .main-menu.open .main-menu-nav a {
      -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
          transform: translate(0, 0);
      -webkit-transition: color .2s ease-in-out 0s, opacity .3s ease, transform .3s ease;
      -webkit-transition: color .2s ease-in-out 0s, opacity .3s ease, -webkit-transform .3s ease;
      transition: color .2s ease-in-out 0s, opacity .3s ease, -webkit-transform .3s ease;
      -o-transition: color .2s ease-in-out 0s, opacity .3s ease, transform .3s ease;
      transition: color .2s ease-in-out 0s, opacity .3s ease, transform .3s ease;
      transition: color .2s ease-in-out 0s, opacity .3s ease, transform .3s ease, -webkit-transform .3s ease;
      opacity: 1;
   }
   .main-menu-nav a {
      opacity: 0;
      display: inline-block;
      padding: 10px 15px;
      font-size: 20px;
      color: #5DADE2;
      -webkit-transform: translate(0, 100%);
      -ms-transform: translate(0, 100%);
          transform: translate(0, 100%);
   }
   .main-menu-nav i {
      color: #fff;
      -webkit-transition: color .2s ease-in-out;
      -o-transition: color .2s ease-in-out;
      transition: color .2s ease-in-out;
   }
   .main-menu.open .main-menu-nav a:hover {
      color: #3498DB;
      /*i don't care*/
      -webkit-transition-delay: 0s !important;
      -o-transition-delay: 0s !important;
         transition-delay: 0s !important;
   }
   .main-menu.open .main-menu-nav a.orange {
      -webkit-transition-delay: .4s;
      -o-transition-delay: .4s;
         transition-delay: .4s;
   }
   .main-menu.open .main-menu-nav a.yellow {
      -webkit-transition-delay: .5s;
      -o-transition-delay: .5s;
         transition-delay: .5s;
   }
   .main-menu.open .main-menu-nav a.green {
      -webkit-transition-delay: .6s;
      -o-transition-delay: .6s;
         transition-delay: .6s;
   }
   .main-menu.open .main-menu-nav a.purple {
      -webkit-transition-delay: .7s;
      -o-transition-delay: .7s;
         transition-delay: .7s;
   }
   .main-menu.open .main-menu-nav a.red {
      -webkit-transition-delay: .8s;
      -o-transition-delay: .8s;
         transition-delay: .8s;
   }
   .main-menu-nav a.orange:hover i {
      color: #D35400;
   }
   .main-menu-nav a.yellow:hover i {
      color: #F5AB35;
   }
   .main-menu-nav a.green:hover i {
      color: #00B16A;
   }
   .main-menu-nav a.purple:hover i {
      color: #9A12B3;
   }
   .main-menu-nav a.red:hover i {
      color: #F64747;
   }
   
   /*toggle button*/
   .main-menu-btn {
      position: absolute;
      width: 40px;
      height: 40px;
      top: 38px;
      right: 39px;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
      border: 2px solid #AEB6BF;
      -webkit-transition: opacity .4s cubic-bezier(0.95, 0.05, 0.795, 0.035), visibility .4s cubic-bezier(0.95, 0.05, 0.795, 0.035);
      -o-transition: opacity .4s cubic-bezier(0.95, 0.05, 0.795, 0.035), visibility .4s cubic-bezier(0.95, 0.05, 0.795, 0.035);
      transition: opacity .4s cubic-bezier(0.95, 0.05, 0.795, 0.035), visibility .4s cubic-bezier(0.95, 0.05, 0.795, 0.035);
   
      -webkit-transform: skew(38deg) rotate(38deg);
      -ms-transform: skew(38deg) rotate(38deg);
          transform: skew(38deg) rotate(38deg);
   
   }
   .main-menu-btn i {
      -webkit-transform: rotate(-38deg);
      -ms-transform: rotate(-38deg);
          transform: rotate(-38deg);
      color: #AEB6BF;
   }
   .main-menu-btn:hover i{
      color: #3498DB;
   }
   
   /*close button*/
   .main-menu.open .main-menu-close {
      opacity: 1;
      -webkit-transition: color .2s ease, opacity 1s ease 1s, transform .3s ease 1s;
      -webkit-transition: color .2s ease, opacity 1s ease 1s, -webkit-transform .3s ease 1s;
      transition: color .2s ease, opacity 1s ease 1s, -webkit-transform .3s ease 1s;
      -o-transition: color .2s ease, opacity 1s ease 1s, transform .3s ease 1s;
      transition: color .2s ease, opacity 1s ease 1s, transform .3s ease 1s;
      transition: color .2s ease, opacity 1s ease 1s, transform .3s ease 1s, -webkit-transform .3s ease 1s;
      -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
          transform: translate(0, 0);
   }
   .main-menu-close {
      opacity: 0;
      display: inline-block;
      margin-top: 65px;
      margin-left: 15px;
      font-size: 18px;
      color: #BDC3C7;
      -webkit-transform: translate(0, 100%);
      -ms-transform: translate(0, 100%);
          transform: translate(0, 100%);
   }
   .main-menu-close:hover {
      color: #A569BD;
   }
   
   /*main content*/
   .main-wrapper {
      max-width: 1280px;
      padding: 0 10px;
      margin: 0 auto;
      background: #212F3D;
   }
   
   .main-content {
      padding: 45px 55px;
   }
   .main-content h4 {
      font-size: 26px;
      line-height: 1.25;
      color: #3498DB;
      text-align: center;
   }
   .main-content .posts li {
      margin: 10px 0;
   }
}


